.section-special-offer {
  padding: 65px 0;
  @media(max-width: 1024px) {
    padding: 30px 0 0 0;
  }
}
.special-offer {
  display: flex;
  justify-content: space-between;
  &__item {
    width: 49%;
  }
  @media(max-width: 767px) {
    flex-wrap: wrap;
    &__item {
      width: 100%;
    }
  }
}
.special-offer-content {
  position: relative;
  @media(max-width: 767px) {
    margin-bottom: 40px;
  }
  img {
    width: 100%;
  }
}
.special-offer-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 295px;
  padding: 20px;
  position: absolute;
  top: 50px;
  right: 0;
  z-index: 2;
  @media(max-width: 767px) {
    position: relative;
    height: 100%;
  }
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 50px;
    background: #fff;
    width: 100%;
    height: 100%;
    z-index: -1;
    @media(max-width: 767px) {
      position: relative;
    }
  }
}
.special-offer-box-inner {
  &:before {
    left: 100px;
    @media(max-width: 767px) {
      position: relative;

    }
  }
}
.special-offer-box__sum {
  color: #161dbe;
  font-size: 120px;
  font-weight: 700;
  line-height: .9;
  text-transform: uppercase;
  @media(max-width: 1024px) {
    font-size: 60px;
  }
  @media(max-width: 767px) {
    font-size: 38px;
  }
}
.special-offer-box__text {
  color: #161dbe;
  font-size: 18px;
  font-weight: 400;
  text-transform: uppercase;
}
.special-offer-small {
  color: #14222b;
  font-size: 24px;
  font-weight: 400;
  line-height: 35px;
  text-transform: uppercase;
  margin-bottom: 7px;
}
.special-offer-subsmall {
  color: #161dbe;
  font-size: 18px;
  font-weight: 700;
  line-height: 35px;
  text-transform: uppercase;
  margin-bottom: 68px;
}
.special-offer-title {
  color: #161dbe;
  font-size: 40px;
  font-weight: 400;
  text-transform: uppercase;
  margin-bottom: 20px;
  @media(max-width: 1024px) {
    font-size: 26px;
    margin: 0 0 30px 0;
  }
  @media(max-width: 767px) {
    font-size: 24px;
    margin: 0 0 20px 0;
  }
}
.special-offer-desc {
  color: #14222b;
  font-size: 24px;
  font-weight: 400;
  text-transform: uppercase;
}

.special-offer-form {
  margin-top: 310px;
  position: relative;
  z-index: 3;

}
.special-offer-form__item {
  position: relative;
  margin-bottom: 20px;
  input {
    width: 100%;
    padding: 10px 0;
    border-bottom: 1px solid #161dbe;
    color:#161dbe;
    font-weight: 700;
    font-size: 12px;
    text-transform: uppercase;
    transition: all 0.2s;
  }
  label {
    display: none;
    color:#161dbe;
    font-weight: 700;
    font-size: 12px;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    transition: all 0.2s;
  }
}
.special-offer-sup {
  color: #14222b;
  font-size: 16px;
  font-weight: 400;
  line-height: 35px;
}
.special-offer-btn {
  width: 100%;
  background: #161dbe;
  border: 1px solid #161dbe;
  padding: 15px;
  color: #fff;
  font-size: 12px;
  font-weight: 700;
  text-align: center;
  transition: all .3s ease-in-out;
  &:hover {
    color: #1b1e21;
    background: transparent;
    border: 1px solid #161dbe;
  }
}
.special-offer-form__item input::-webkit-input-placeholder {
  color:#161dbe;
  font-weight: 700;
  font-size: 12px;
  text-transform: uppercase;
  transition: all .3s ease-in-out;

}
.special-offer-form__item input::-moz-placeholder          {
  color:#161dbe;
  font-weight: 700;
  font-size: 12px;
  text-transform: uppercase;
  transition: all .3s ease-in-out;

}/* Firefox 19+ */
.special-offer-form__item input:-moz-placeholder           {
  color:#161dbe;
  font-weight: 700;
  font-size: 12px;
  text-transform: uppercase;
  transition: all .3s ease-in-out;
}/* Firefox 18- */
.special-offer-form__item input:-ms-input-placeholder      {
  color:#161dbe;
  font-weight: 700;
  font-size: 12px;
  text-transform: uppercase;
  transition: all .3s ease-in-out;

}
