.section-cost-rent {
  padding: 60px 0;
  @media(max-width: 1024px) {
    padding: 30px 0;
  }
}
.cost-rent-title {
  text-transform: uppercase;
  color: #14222b;
  font-size: 24px;
  font-weight: 400;
  margin-bottom: 60px;
  span {
    color: #161dbe;
    font-weight: 700;
    margin-left: 7px;
  }
  @media(max-width: 1024px) {
    margin-bottom: 30px;
  }
  @media(max-width: 768px) {
    font-size: 20px;
  }
}
.cost-rent {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  &:after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 60px;
    width: calc(100% - 23% * 3);
    height: 1px;
    background: #161dbe;
    @media(max-width: 767px) {
      display: none;
    }
  }
}
.cost-rent__item {
  position: relative;
  width: 23%;
  margin: 60px 20px 60px 0;
  padding: 40px 20px 40px 50px;
  @media(max-width: 1024px) {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 20px;
    width: 48%;
    margin: 30px 0;
  }
  @media(max-width: 767px) {
    width: 100%;
  }
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 20px;
    width: 100px;
    height: 100%;
    background: #e5e6eb;
    z-index: -1;
  }
  svg {
    fill: #161dbe;
    width: 100px;
    height: 100px;
    margin-bottom: 25px;
    @media(max-width: 767px) {
      width: 70px;
      height: 70px;
    }
  }
  p {
    text-transform: uppercase;
    color: #161dbe;
    font-weight: 300;
    font-size: 16px;
    line-height: 24px;
    @media(max-width: 767px) {
      font-size: 14px;
    }
  }
}
