.section-coworking-price {
  min-height: 500px;
  background: url("../img/coworking-bg.png") no-repeat center;
  background-size: cover;
  padding: 60px 0;
  @media(max-width: 1024px) {
    padding: 30px 0;
  }
  .main-container {
    height: 100%;
  }
}
.coworking-price-title {
  color: #fff;
  text-transform: uppercase;
  font-size: 24px;
  font-weight: 400;
  margin-bottom: 60px;
  @media(max-width: 1024px) {
    margin-bottom: 30px;
  }
}
.coworking-price-desc {
  font-weight: 700;
  margin-left: 7px;
}
.coworking-price {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  &__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 30%;
    padding: 15px;
    margin-bottom: 25px;
    @media(max-width: 1024px) {
      width: 49%;
      align-items: center;
    }
    @media(max-width: 767px) {
      width: 100%;
    }
  }
}
.price-content__title {
  font-size: 30px;
  line-height: 40px;
  color: #fff;
  text-transform: uppercase;
  margin-bottom: 25px;
  @media(max-width: 1024px) {
    font-size: 25px;
    line-height: normal;
  }
  @media(max-width: 767px) {
    font-size: 20px;
    p {
      display: inline-block;
    }
  }
}
.price-content__title-title {
  font-weight: 700;
}
.price-content__title-desc {
  font-weight: 400;
}
.price-content__desc {
  font-size: 18px;
  font-weight: 400;
  margin-bottom: 50px;
  color: #fff;
  @media(max-width: 1024px) {
    font-size: 14px;
  }
}
.price-content__num {
  text-transform: uppercase;
  font-size: 24px;
  font-weight: 700;
  color: #fff;
  letter-spacing: 2.4px;
  margin-bottom: 25px;
  @media(max-width: 1024px) {
    font-weight: 18px;
  }
}
.price-content__btn {
  max-width: 260px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 15px;
  border: 2px solid #ffffff;
  background: transparent;
  color: #fff;
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1.2px;
  transition: all .3s ease-in-out;
  &:hover {
    border-color: #161dbe;
    color: #fff;
  }
}
