.section-for-whom {
  padding-bottom: 60px;
  @media(max-width: 1024px) {
    padding-bottom: 30px;
  }
}
.for-whom__title {
  font-weight: 900;
  font-size: 40px;
  color: #161dbe;
  text-transform: uppercase;
  margin: 0 0 90px 0;
  @media(max-width: 1024px) {
    font-size: 26px;
    margin: 0 0 30px 0;
  }
  @media(max-width: 767px) {
    font-size: 24px;
    margin: 0 0 20px 0;
  }
}
.for-whom {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  &__item {
    position: relative;
    width: 32%;
    padding: 0 15px 15px 160px;
    color: #161dbe;
    font-size: 16px;
    font-weight: 400;
    text-transform: uppercase;
    margin-bottom: 20px;
    @media(max-width: 1170px) {
      font-size: 14px;
    }
    @media(max-width: 1024px) {
      width: 49%;
    }
    @media(max-width: 767px) {
      width: 100%;
      padding-left: 100px;
    }
    &:before {
      content: "";
      position: absolute;
      left: 40px;
      top: -10px;
      width: 38px;
      height: 39px;
      background: #e5e6eb;
      z-index: 2;
      @media(max-width: 767px) {
        left: 25px;
        width: 25px;
        height: 25px;
      }
    }
    &:after {
      content: "";
      position: absolute;
      top: 5px;
      left: 0;
      width: 120px;
      height: 1px;
      background: #161dbe;
      @media(max-width: 767px) {
        width: 80px;
      }
    }
  }
}
.for-whom__item.for-whom__item-two {
  &:after {
    top: 10px;
  }
}
.for-whom__item.for-whom__item-three {
  &:after {
    top: 15px;
  }
}
.for-whom__item.for-whom__item-four {
  &:after {
    top: 15px;
  }
}
.for-whom__item.for-whom__item-five {
  &:after {
    top: 10px;
  }
}
