.section-offer-clients {
  margin: 60px 0;
  min-height: 800px;
  @media(max-width: 1024px) {
    margin: 30px 0;
  }
}

h3.offer-clients__title {
  color: #161dbe;
  text-transform: uppercase;
  font-size: 24px;
  margin-bottom: 40px;
  @media(max-width: 1024px) {
    margin-bottom: 30px;
    font-size: 20px;
  }
  @media(max-width: 768px) {
    font-size: 18px;
  }
}
.offer-clients__title p {
  color: #14222b;
  display: inline-block;
  @media(max-width: 1024px) {
    display: block;
  }
}
.offer-clients-wrapp {
  display: flex;
  justify-content: space-between;
}
.offer-clients {
  width: calc(100% - 500px);
  @media(max-width: 1024px) {
    width: 100%;
  }
}
.offer-content {
  margin-left: 240px;
  margin-right: auto;
  margin-bottom: 45px;

  @media(max-width: 1240px) {
    width: 100%;
    margin-left: auto;
  }
}
.offer-clients-inner {
  position: absolute;
  top: 0;
  right: 0;
  width: 500px;
  height: 100%;
  background-image: url('../img/box12.png');
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;
  @media(max-width: 1024px) {
    display: none;
  }
}
.offer-content__desc {
  font-size: 16px;
  color: #14222b;
  line-height: 24px;
  text-transform: uppercase;
  margin-bottom: 45px;
  span {
    color: #161dbe;
    font-weight: 600;
  }
  @media(max-width: 767px) {
    font-size: 14px;
  }
}
.offer-content__title {
  font-size: 24px;
  font-weight: 600;
  color: #161dbe;
  line-height: 34px;
  text-transform: uppercase;
  margin-bottom: 45px;
  @media(max-width: 1024px) {
    font-size: 20px;
  }
  @media(max-width: 767px) {
    font-size: 18px;
  }
}
.offer-content__small {
  font-size: 16px;
  color: #14222b;
  line-height: 24px;
  text-transform: uppercase;
  margin-bottom: 60px;
  &-inner {
    margin-bottom: 0;
  }
  @media(max-width: 767px) {
    font-size: 14px;
  }
}
.offer-content__small-blue {
  font-size: 16px;
  font-weight: 600;
  display: block;
  color: #161dbe;
  line-height: 24px;
  text-transform: uppercase;
  margin-bottom: 60px;
  @media(max-width: 767px) {
    font-size: 14px;
  }
}
.offer-content__link {
  position: relative;
  color: #fff;
  font-size: 12px;
  text-transform: uppercase;
  background: #161dbe;
  max-width: 385px;
  min-height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  @media(max-width: 1024px) {
    margin-left: auto;
    margin-right: auto;
  }
}
