.section-questions {
  height: 500px;
  background: url("../img/block8.png")no-repeat center center;
  background-size: cover;
  .main-container {
    height: 100%;
  }
}
.questions {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  @media(max-width: 767px) {
    flex-wrap: wrap;
  }
  &-content {
    display: flex;
    justify-content: space-between;
    @media(max-width: 767px) {
      flex-wrap: wrap;
    }
  }
  &__item {
    width: 50%;
    position: relative;
    @media(max-width: 767px) {
      width: 100%;
    }
  }
  &-btn {
    position: absolute;
    left: 0;
    width: 100%;
    min-width: 279px;
    padding: 15px;
    background: #161dbe;
    overflow: hidden;
    color: #ffffff;
    font-size: 12px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 1.2px;
    transition: all .3s ease-in-out;
    &:after {
      background: #fff;
      content: "";
      height: 155px;
      left: -75px;
      opacity: .2;
      position: absolute;
      top: -50px;
      transform: rotate(35deg);
      transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
      width: 50px;
      //z-index: -10;
    }
    &:hover {
      color: #fff;
      background: #161dbe;
      &:after {
        left: 120%;
        transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
      }
    }
  }
}
.questions-title {
  font-size: 24px;
  font-weight: 700;
  line-height: 35px;
  text-transform: uppercase;
  color: #fff;
}
.questions-desc {
  color: #ffffff;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  margin-bottom: 25px;
}

