.company {
  display: flex;
  justify-content: space-between;
  @media(max-width: 921px) {
    flex-wrap: wrap;
  }
  &-item {
    position: relative;
    display: flex;
    width: 32.5%;
    cursor: pointer;
    //overflow: hidden;
    margin: 45px 0;

    @media(max-width: 921px) {
      width: 100%;
      overflow: hidden;
      height: 600px;
      margin: 10px;
    }
  }
  &-link {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;

    img {
      @media(max-width: 921px) {
        //width: 100%;
        height: auto;
        position: absolute;
        left: -9999px;
        right: -9999px;
        margin: 0 auto;
      }
    }
  }
  &-box-show {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 45px 30px;
  }
  &-title {
    color: #ffffff;
    font-size: 36px;
    text-transform: uppercase;
    font-weight: 700;
    text-align: center;
    margin-bottom: 25px;
    @media(max-width: 921px) {
      font-size: 24px;
    }
  }
  &-subtitle {
    color: #ffffff;
    font-size: 24px;
    font-weight: 400;
    margin-bottom: 25px;
    @media(max-width: 921px) {
      font-size: 18px;
    }
  }
  &-desc {
    color: #ffffff;
    font-size: 24px;
    font-weight: 700;
    margin-bottom: 25px;
    @media(max-width: 921px) {
      font-size: 18px;
    }
  }
  &-show-title {
    color: #ffffff;
    font-size: 23px;
    line-height: 21px;
    text-transform: uppercase;
    font-weight: 700;
    margin-bottom: 25px;
    @media(max-width: 921px) {
      font-size: 16px;
    }
  }
}

.company-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  top: 100%;
  left: 0;
  height: calc(100% + 90px);
  width: 100%;
  transition: all .5s;
  padding: 0 30px;
  background: #161dbe;
  z-index: 2;
  opacity: 0;
  @media(max-width: 921px) {
    height: 600px;
    overflow-y: scroll;
  }
}
.company-content__link {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 150px;
  width: calc(100% - 60px);
  position: relative;
  padding: 15px;
  color: #fff;
  font-size: 12px;
  font-weight: 700;
  background: #161dbe;
  border: 1px solid transparent;
  transition: all .3s ease-in-out;
  @media(max-width: 960px) and (min-width: 768px) {
    max-width: 180px;
  }

  &:hover {
    border-color: #fff;
    color: #fff;
  }
}
.company-content.open {
  top: -45px;
  opacity: 1;
  @media(max-width: 921px) {
    top: 0;
  }
}
