.section-coworking-about {
  padding: 60px 0;
  @media(max-width: 1024px) {
    padding: 30px 0;
  }
}
.coworking-about {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 80px 0;
  @media(max-width: 1024px) {
    flex-wrap: wrap;
    padding: 20px 0;
  }
  &:before {
    content: "";
    width: calc(50% - 15px);
    height: 100%;
    position: absolute;
    top: 0;
    left: 50%;
    margin-right: 15px;
    background: #161dbe;
    z-index: -1;
    @media(max-width: 1024px) {
      display: none;
    }
  }
  &-content {
    width: 70%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 90px;
    @media(max-width: 1024px) {
      margin-top: 0;
      width: 100%;
    }
  }
  &-title-wrapp {
    margin-bottom: 60px;
    @media(max-width: 1024px) {
      margin-bottom: 30px;
    }
  }
  &-title {
    color: #14222b;
    font-size: 24px;
    font-weight: 400;
    text-transform: uppercase;
    margin-bottom: 60px;
  }
  &-subtitle {
    color: #161dbe;
    font-weight: 700;
    margin-left: 5px;
    @media(max-width: 767px) {
      display: block;
      margin-left: 0;
    }
  }
  &-desc {
    width: 1000px;
    margin: auto;
    color: #14222b;
    font-size: 24px;
    font-weight: 400;
    text-transform: uppercase;
    @media(max-width: 1000px) {
      width: 100%;
      font-size: 18px;
    }
  }
  &__item {
    display: flex;
    justify-content: center;
    padding: 30px;
    width: 32%;
    min-height: 469px;
    @media(max-width: 1024px) {
      align-items: center;
      width: 100%;
      margin-bottom: 20px;
      padding: 20px;
      min-height: 100%;
    }
  }
}
.coworking-about-content__title {
  font-size: 40px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 35px;
  @media(max-width: 1024px) {
    font-size: 26px;
    margin: 0 0 30px 0;
  }
  @media(max-width: 767px) {
    font-size: 24px;
    margin: 0 0 20px 0;
  }
}
.coworking-about-content__desc {
  font-size: 20px;
  font-weight: 400;
  line-height: 26px;
}
.coworking-about__item-blue {
  background: #161dbe;
  color: #fff;
}
.coworking-about__item-grey {
  background: #e5e6eb;
  color: #14222b;
}
.coworking-about__item-white {
  background: #fff;
  color: #14222b;
}
