.section-coworking-box {
  position: relative;
  min-height: 530px;
  background-size: cover;
  //background: url("../img/box10.png");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  .main-container {
    height: 100%;
  }
}
.coworking-box {
  width: 80%;
  margin: auto;
  height: 100%;
  padding: 80px 0;
  @media(max-width: 1024px) {
    padding: 60px 0;
  }
  @media(max-width: 768px) {
    width: 100%;
  }
  &__desc {
    font-size: 24px;
    font-weight: 600;
    color: #fff;
    text-transform: uppercase;
    margin-bottom: 25px;
    @media(max-width: 768px) {
      font-size: 16px;
    }
  }
  &__title {
    font-size: 40px;
    font-weight: 700;
    color: #fff;
    text-transform: uppercase;
    margin-bottom: 25px;
    @media(max-width: 1024px) {
      font-size: 30px;
    }
    @media(max-width: 768px) {
      font-size: 18px;
    }
  }
  &__small {
    font-size: 16px;
    font-weight: 400;
    color: #fff;
    line-height: 24px;
    text-transform: uppercase;
    margin-bottom: 25px;
    @media(max-width: 768px) {
      font-size: 12px;
    }
  }
}
