.section-resolution {
  padding: 60px 0;
  @media(max-width: 1024px) {
    padding: 0 0 30px 0;
  }
}
.resolution {
  @media(max-width: 1024px) {
    flex-wrap: wrap;
    .col-md-4 {
      max-width: 100%;
      flex: 100%
    }
  }

  &-title {
    font-size: 24px;
    font-weight: 400;
    color: #14222b;
    text-transform: uppercase;
    margin-bottom: 60px;
    @media(max-width: 1024px) {
      margin-bottom: 30px;
      font-size: 20px;
    }
  }
  &__item {
    height: 472px;
    background-color: #e5e6eb;
    padding: 50px 50px 50px 50px;
    margin-bottom: 20px;
    @media(max-width: 1024px) {
      width: 100%;
    }
    @media(max-width: 767px) {
      padding: 20px;
      height: auto;
    }
    &-two {
      height: 411px;
      @media(max-width: 767px) {
        height: auto;
      }
    }
    &-three {
      height: 545px;
      @media(max-width: 767px) {
        height: auto;
      }
    }
  }
}
.resolution-content {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;

  &__title {
    color: #161dbe;
    font-weight: 700;
    font-size: 24px;
    text-transform: uppercase;
    @media(max-width: 1024px) {
      margin-bottom: 25px;
      font-size: 22px;
    }
  }
  &__desc {
    color: #161dbe;
    font-size: 16px;
    font-weight: 400;
    text-transform: uppercase;
  }
}
